@import url(https://fonts.googleapis.com/css?family=Roboto:400,500,300,700);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

table{
    width:100%;
    table-layout: fixed;
}

.tbl-header{
    background-color: transparent;
    margin: 20px;
}

.tbl-content{
    overflow-x:auto;
    margin-top: 0px;
    max-width: 100%;
}


td{
    padding: 15px;
    text-align: center;
    vertical-align:middle;
    font-weight: 300;
    font-size: 12px;
    color: #fff;
    border-bottom: solid 1px rgba(255,255,255,0.1);
}
body{
  background: linear-gradient(to right, #85bdb5cc, #f7a445b4);
  font-family: 'Roboto', sans-serif;
}
/* body{
    background-color: rgb(177, 136, 255);
  } */



.inputPrompt {
    font-size: 20px;
    height: 40px;
    box-shadow: none;
    padding-left: 15px;
    padding-right: 15px;
    width: 80%;
    font-weight: 300;
    cursor: text;
}

.inputField {
    margin: 20px;
}
input, textarea{
    background-color: transparent;
    border-color: transparent;
    border-radius: 100px;
    background-color: rgb(243, 243, 243);
    max-width: 100%;
}

input:focus {
    outline: none;
    background-color: rgb(255, 255, 255);
    border-color: rgb(206, 108, 42);
    border-radius: 100px;
    border-style:inherit ;
}

.inputField button {
    background-color: transparent;
    border: none;
    vertical-align: auto;
    max-width: 100%;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.close-svg {
    width: 30px;
    background-color:transparent;
    color:red;
    cursor: pointer;
}

.add-svg {
    width: 30px;
    background-color:transparent;
    color:rgb(2, 187, 11);
    cursor: pointer;
}

button {
    background-color: rgb(243, 243, 243);
    border-style: hidden;
    border-radius: 20px;
}

@media (max-width: 720px) {
    .inputPrompt {
        overflow:scroll;
        font-size: 15px;
        height: 25px;
        box-shadow: none;
        max-width: 60%;
        font-weight: 100;
        cursor: text;
    }

    .inputContainer {
        margin: 2px;
        text-align: center;
    }

    td{
        padding: 0;
        margin: 0;
        text-align: center;
        vertical-align:middle;
        color: #fff;
        border-bottom: solid 1px rgba(255,255,255,0.1);
    }

    .numbers {
        width: 30%;
    }

    .tbl-content{
        overflow-x:hidden;
        margin-top: 0px;
        max-width: 100%;
    }

    .inputField {
        margin: 5px;
    }

}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

h1 {
  text-align: left;
  font-size: 30px;
  color: rgb(228, 104, 22);
  text-shadow: 1px 1px 1px rgb(19, 18, 18);;
  font-weight: bold;
  font-family: Georgia, 'Times New Roman', Times, serif;
}

.App-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

